.section-hero {
  background-color: rgba(0, 0, 0, 0.1); }

.hero-overlay {
  background-attachment: fixed;
  background-image: url("/static/images/banner.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  left: 0;
  opacity: 1;
  position: absolute;
  top: 0;
  transition: opacity 5s;
  width: 100vw;
  z-index: -1; }
  .hero-overlay--loading {
    opacity: 0; }

.hero-text {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100vh;
  padding-bottom: 16vh; }
  .hero-text h2 {
    font-size: 1.75em;
    font-weight: 800;
    letter-spacing: 0.225em;
    line-height: 1.75em;
    margin-bottom: 1em;
    opacity: 1;
    transition: opacity 5s; }
    .hero-text h2.loading {
      opacity: 0; }
  .hero-text p {
    margin: 0;
    opacity: 1;
    transition: opacity 5s; }
    .hero-text p.loading {
      opacity: 0; }

@media screen and (max-width: 736px) {
  .hero-text__h2 {
    font-size: 1.25em; } }



